nav {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    margin-bottom: 20px;
    font-size: 13px;
    background: transparent;
    background: #c9b8ba59;
    align-items: center;
}
nav a{
    text-decoration: none;
    color: inherit;
}
.logo{
    margin-right: 50px;
    margin-left: 15px;
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 4px;
}

.list{
    list-style: none;
    display: flex;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-right: 20px;
    gap: 25px;
}
#toggler, nav label{
    display: none;
}

@media screen and (max-width:600px) {
    .menu{
        width: 100vw;
        max-height: 0;
        overflow: hidden;
    }
    .list{
        flex-direction: column;
        align-items: center;
        padding: 25px;
    }
    nav label{
        display: inline-flex;
        align-items: center;
        cursor: pointer;
    }
    #toggler:checked ~ .menu{
        max-height: 100%;
    }
}